@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
body {
  min-width: 320px;
  margin: 0 auto;
  min-height: 100%;
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 129%;
  color: #444444;
  background-color: #ffffff;
}

.visually-hidden {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  margin: -1px;
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.container {
  width: 320px;
  padding: 0 20px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .container {
    width: 708px;
    padding: 0 30px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1220px;
    padding: 110px;
  }
}
.header {
  padding: 10px 0 17px;
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 100%;
  background-color: #ffffff;
}

.header__toggle {
  width: 24px;
  height: 16px;
  border: none;
}

.header__flex-wrapper {
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: 768px) {
  .header {
    width: 708px;
    display: grid;
    grid-template-columns: 174px 1fr;
  }

  .header__flex-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding: 0;
  }

  .header__toggle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .header {
    width: 1220px;
  }
}
.logo {
  display: block;
  margin: 0;
  padding: 0;
  width: 33px;
  height: 37px;
}
.logo:hover .logo__img {
  opacity: 0.8;
}
.logo:focus .logo__img {
  opacity: 0.6;
}

@media (min-width: 768px) {
  .logo {
    display: none;
  }
}
.logo-main {
  display: block;
  width: 101px;
  height: 18px;
  padding: 0;
  margin: 0;
}

.logo-main:hover .logo-main__img {
  opacity: 0.8;
}

.logo-main:focus .logo-main__img {
  opacity: 0.6;
}

@media (min-width: 768px) {
  .logo-main {
    display: none;
  }
}
.logo-alternative {
  display: none;
}

@media (min-width: 768px) {
  .logo-alternative {
    display: block;
  }
}
.menu {
  margin: 0;
  padding: 0;
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 100%;
  color: #000000;
  background-color: #ffffff;
}

.menu__content-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.menu__content-link {
  white-space: nowrap;
  display: block;
  padding: 24px 20px;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 100%;
}
.menu__content-link:hover {
  opacity: 0.6;
}
.menu__content-link:focus {
  opacity: 0.3;
}
.menu__content-link:active {
