.programs__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-family: "Oswald", sans-serif;
}

.programs__item {
  position: relative;
  margin: 0 20px 20px;
  padding: 21px 20px 0;
  background-color: #f2f2f2;
}

.programs__item--slim::before {
  position: absolute;
  content: " ";
  width: 50px;
  height: 53px;
  top: 21px;
  left: 22px;
  background-image: url("/img/icon/cat_slim.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.programs__item--pro::before {
  position: absolute;
  content: " ";
  width: 67px;
  height: 50px;
  top: 24px;
  left: 13px;
  background-image: url("/img/icon/cat_pro.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.programs__item-subtitle {
  margin: 0 0 20px 71px;
  padding: 9px 0;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 154%;
  text-transform: uppercase;
  color: #000000;
}

