  padding: 0;
  margin: 0;
  margin-bottom: 23px;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 129%;
}

.programs__wrapper-link {
  display: flex;
  align-items: center;
  padding: 15px 20px 18px;
  border-top: 1px solid #d9d9d9;
}

.programs__link {
  display: block;
  padding: 4px 15px 4px 0;
  font-size: 16px;
  line-height: 100%;
  text-transform: uppercase;
  color: #000000;
  text-decoration: none;
}

.programs__link-icon {
  padding: 0;
}

@media (min-width: 768px) {
  .programs {
    width: 708px;
  }

  .programs__item {
    display: flex;
    flex-direction: column;
    margin: 0;
    margin-bottom: 30px;
    padding: 77px 62px 53px 52px;
  }

  .programs__item--slim::before {
    width: 200px;
    height: 211px;
    top: 81px;
    left: 446px;
  }

  .programs__item--pro::before {
