.menu {
  margin: 0;
  padding: 0;
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 100%;
  color: $color-black;
  background-color: $color-white;
}

.menu__content-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.menu__content-link {
  white-space: nowrap;
  display: block;
  padding: 24px 20px;
  text-transform: uppercase;
  text-decoration: none;
  color: $color-black;
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 100%;

  &:hover {
    opacity: 0.6;
  }

  &:focus {
    opacity: 0.3;
  }

  &:active {
    opacity: 1;
  }
}

.menu__content-item {
  padding: 0;
}

@media (min-width: $tablet-width) {
  .menu {
    width: 440px;
  }

  .menu__content-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
